<template>
	<view class="cityAlaways safeBottom250">
		<view class="inputCity flex row-center">
			<input type="text" v-model="inputVal" placeholder="请输入城市" />
			<view class="search">检索</view>
		</view>
		<view class="list" v-show="inputVal.length>0">
			<view v-for="(item,index) in serachArr" :key="index" class="font30 flex area-between"
				@click="toggle(index)">
				{{item.name}}
				<image src="../../static/image/yes.png" mode="" class="yes" v-show="item.checked"></image>
			</view>
		</view>
		<picker-view :value="chooseIndex" @change="bindChange" class="picker-view">
			<picker-view-column>
				<view class="item" v-for="(item,index) in citys" :key="index">{{item.label}}</view>
			</picker-view-column>
			<picker-view-column>
				<view class="item" v-for="(item,index) in citys[chooseIndex[1]].children" :key="index">{{item.label}}
				</view>
			</picker-view-column>
		</picker-view>
		<view class="fixedBottom safeBottom">
			<view class="confirm" @click="confirm">确定</view>
		</view>
	</view>
</template>

<script>
	import {
		city
	} from "../../utils/citydict.js";
	import {
		citys
	} from "../../utils/city-data.js"
	export default {
		data() {
			return {
				citylist: [],
				inputVal: '',
				serachArr: [],
				citys: citys,
				chooseIndex: [0, 0]
			};
		},
		onLoad() {
			this.getData()
		},
		watch: {
			inputVal(newData) {
				if (newData.length > 0) {
					let n = this.citylist,
						arr = [];
					for (let i = 0, j = n.length - 1; i <= j;) {
						if (n[i].name.indexOf(newData) != -1) {
							arr.push(n[i]);
						} else if (n[j].name.indexOf(newData) != -1) {
							arr.push(n[j])
						}
						i++;
						j--;
					}
					this.serachArr = arr;
				}
			}
		},
		methods: {
			getData() {
				city.map(item => {
					item.data.forEach((tag, index) => {
						this.citylist.push({
							id: index + 1,
							checked: false,
							name: tag
						})
					})
				})
				console.log(this.citys)
			},
			toggle(index) {
				this.serachArr[index].checked = !this.serachArr[index].checked;

			},
			bindChange(e) {
				console.log(e.detail.value)
			},
			confirm() {
				let arr = [];
				for (let i in this.citylist) {
					if (item.checked) {
						arr.push(item.name)
					}
				}
				if (arr.length == 0) {
					this.$toast('请选择常驻城市');
					return;
				}
				let pages = getCurrentPages();
				let prevPage = pages[pages.length - 2];
				console.log(prevPage)
				if (prevPage.route == 'pages/index/filtrate') {
					prevPage.$vm.residentCity = arr.join(',');
				} else {
					prevPage.$vm.form.residentCity = arr.join(',');
				}
				uni.navigateBack();
			}
		}
	}
</script>

<style lang="scss" scoped>
	.cityAlaways {}

	.picker-view {
		width: 100%;
		height: 600rpx;
		margin-top: 20rpx;
	}

	.item {
		height: 80rpx;
		line-height: 80rpx;
		align-items: center;
		justify-content: center;
		text-align: center;
	}

	.confirm {
		width: 690rpx;
		height: 90rpx;
		line-height: 90rpx;
	}

	.inputCity {
		width: 100%;
		position: sticky;
		background-color: #FFFFFF;
		padding: 20rpx 30rpx;
		top: 0;

		input {
			flex: 1;
			border-radius: 20rpx;
			border: 1px solid #eee;
			font-size: 32rpx;
			padding: 0 20rpx;
			margin: 0 20rpx 0 0;
			height: 80rpx;
		}

		.search {
			width: 80rpx;
			height: 60rpx;
			line-height: 60rpx;
			text-align: center;
			color: #FFFFFF;
			background: #FF4364;
			border-radius: 10rpx;
			font-size: 30rpx;
		}
	}

	.list {
		>view {
			height: 80rpx;
			line-height: 80rpx;
			padding: 0 30rpx;
			border-bottom: 1px solid #eee;

			.yes {
				width: 34rpx;
				height: 24rpx;
			}
		}
	}
</style>
